@import "../../css-config/mixins.scss";
.contact-us {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    position: relative;
    @include md {
        min-height: 80vh;
    }
    .background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(to right, #4d7af3, #ff6ac1);
        opacity: 0.8;
        z-index: -1;
    }
    .content {
        padding: 40px;
        max-width: 800px;
        background: #fff;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
        border-radius: 10px;
        text-align: center;
        @include md {
            padding: 60px;
            max-width: 1000px;
        }
        .cool-header {
            font-size: 3rem;
            color: #4d7af3;
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
            margin-bottom: 20px;
            line-height: 1.2;
            @include md {
                font-size: 3.5rem;
                margin-bottom: 30px;
            }
        }
        .contact-details {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 40px;
            @include md {
                margin-top: 60px;
            }
            .contact-item {
                display: flex;
                align-items: center;
                margin-bottom: 20px;
                .icon {
                    font-size: 2rem;
                    margin-right: 10px;
                }
                p {
                    font-size: 1.2rem;
                    a {
                        color: #4d7af3;
                        text-decoration: none;
                        transition: color 0.3s;
                        &:hover {
                            color: #ff6ac1;
                        }
                    }
                }
            }
        }
    }
}